<template>
  <div class="pass-wrapper">
    <form action ref="form-wrapper" class="form-wrapper">
      <div class="input-wrapper">
        <div class="label">新密码</div>
        <div class="text-input">
          <input type="password" v-model="ruleForm.pass" placeholder="请输入新的密码" />
        </div>
      </div>
      <div class="input-wrapper bottom-mag">
        <div class="label">确认密码</div>
        <div class="text-input">
          <input type="password" v-model="ruleForm.checkPass" placeholder="请再次输入新的密码" />
        </div>
      </div>
      <div class="desc-wrapper">
        <div class="left-wrapper">密码必须包括8个字符</div>
      </div>
      <div class="submit-wrapper">
        <button type="submit" @click.prevent="submitForm" class="submit">完成</button>
      </div>
    </form>
  </div>
</template>

<script>
import { isPassWord } from "@/utils/validate";
import { setPassword } from "@/api/user";
import { setEncryption } from "@/mixins/user";
export default {
  mixins: [setEncryption],
  data() {
    return {
      phone: this.$route.params.phone,
      PUBLIC_KEY: null,
      ruleForm: {
        pass: "",
        checkPass: "",
      }
    };
  },
  watch: {},
  computed: {},
  methods: {
    async rules() {
      if (this.ruleForm.pass !== "") {
        if (!isPassWord(this.ruleForm.pass)) {
          this.$toast("密码不符合规则");
          return false;
        }
      } else {
        this.$toast("请输入密码");
        return false;
      }
      if (this.ruleForm.checkPass !== "") {
        if (this.ruleForm.checkPass !== this.ruleForm.pass) {
          this.$toast("两次输入密码不一致");
          return false;
        }
      } else {
        this.$toast("请再次输入密码");
        return false;
      }
      return true;
    },
    submitForm(event) {
      this.$reCaptcha(event, "setPassword");
      this.rules().then(valid => {
        if (valid) {
          let _this = this;
          let obj = JSON.stringify({
            phone: this.phone,
            password: this.encryption(this.ruleForm.pass)
          });
          setPassword(obj).then(res => {
            _this.$toast.loading({
              message: "设置成功!跳转中...",
              forbidClick: true,
              loadingType: "spinner"
            });
            _this.$store.dispatch("getAgent_token")
            .catch(error => {
              _this.$router.push({ name: 'm_account' })
            })
          });
        }
      });
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/mobile/index.scss";
.pass-wrapper {
  width: 100%;
  height: 100%;
  .form-wrapper {
    width: 100%;
    padding: 40px;
    box-sizing: border-box;
    .label {
      width: 4rem;
    }
    .desc-wrapper {
      padding-top: 10px;
      box-sizing: border-box;
      width: 100%;
      font-size: 13px;
      text-align: left;
      color: #999999;
      margin-bottom: 30px;
    }
  }
}
</style>


